
.page2 {
  background-color: black;
  background: url("https://images.unsplash.com/photo-1422226256160-9b266e308ea6?dpr=1&auto=format&fit=crop&w=1500&h=996&q=80&cs=tinysrgb&crop=");
  background-size: cover;
  height: 800px;
}
.profilePic {
  margin-top: 20%;
}
h1 {
  margin-top: 0;
  color: white;
}
img {
  display: inline;
}
.color2 {
  background: #4568DC;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #B06AB3, #4568DC);
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(left, #B06AB3, #4568DC);
  background: linear-gradient(to right, #B06AB3, #4568DC);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.color3 {
  background: #314755;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #314755, #26a0da);
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(right, #314755, #26a0da);
  background: linear-gradient(to left, #314755, #26a0da);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.color1 {
  background: #348F50;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #348F50, #56B4D3);
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(right, #348F50, #56B4D3);
  background: linear-gradient(to left, #348F50, #56B4D3);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.carousel {
  height: 250px;
}
.about-mes {
  font-family: Raleway;
  color: white;
  display: block;
  /*background-color: transparent !important;*/
  word-wrap: break-all;
  height: 250px;
  width: 100%;
  font-size: 24px;
}



